import React, { useContext, useState } from 'react'
import { NavLink } from "react-router-dom"
import AuthContext from '../../../context/auth/authContext'
import TiendaContext from '../../../context/tienda/tiendaContext'
import { getProductos, getProductosBuscador } from '../../../services/productServices'
import "./../styles/tienda.css"

const TiendaHeader = () => {

    const { autenticado } = useContext(AuthContext);

    const { globalZonaVentaCarta, muestraGlobalZonaVentaCarta,
        globalZonaVentaMapa, muestraGlobalZonaVentaMapa,
        globalBotonMapa, muestraGlobalBotonMapa, seleccionarCategoria } = useContext(TiendaContext);

    // Busqueda de producto:
    const [busquedaProducto, setBusquedaProducto] = useState({
        producto_buscado: ""
    });

    const handleChange = (e) => {
        setBusquedaProducto({
            ...buscarProducto,
            [e.target.name]: e.target.value,
        })
    };

    const traerProductos = async () => {
        getProductos().then(() => {

        })
    }

    const buscarProducto = async () => {
        if (busquedaProducto.producto_buscado) {
            getProductosBuscador(busquedaProducto.producto_buscado).then((objProductos) => {
                if (objProductos.length > 0) {
                    console.log(objProductos);
                    seleccionarCategoria(objProductos)
                }
            })

        }
    }

    const submitChange = (e) => {
        e.preventDefault();
        buscarProducto();
    }

    // Se oculto, el boton para ver la Carta de Productos
    const checkVistaCarta = () => {
        console.log(globalZonaVentaCarta);
        muestraGlobalZonaVentaCarta(!globalZonaVentaCarta)
    }

    // Botos de activar y desactiva la visualizaciÃ³n del Mapa
    const checkVistaMapa = () => {
        muestraGlobalZonaVentaMapa(!globalZonaVentaMapa)
    }

    const checkBotonMapa = (btnMapa) => {
        muestraGlobalBotonMapa(btnMapa);
    }

    return (

        <nav className="navbar navbar-expand-sm navbar-dark header__navegar">
            <a className="navbar-brand" href="#">T-COVID</a>
            <button className="navbar-toggler d-lg-none" type="button" data-toggle="collapse" data-target="#collapsibleNavId" aria-controls="collapsibleNavId"
                aria-expanded="false" aria-label="Toggle navigation"></button>
            <div className="collapse navbar-collapse" id="collapsibleNavId">
                <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
                    <li className="nav-item">
                        <NavLink
                            to={"/tienda/pedidos"}
                            className="nav-link"
                            activeClassName={"active"}
                            onClick={() => {
                                checkBotonMapa(true)
                            }}
                        >Pedidos</NavLink>
                    </li>

                    {
                        autenticado ?
                            <li className="nav-item">
                                <NavLink
                                    to={"/tienda/indicadores"}
                                    className="nav-link"
                                    activeClassName={"active"}
                                    onClick={() => {
                                        checkBotonMapa(false)
                                    }}
                                >Indicadores</NavLink>
                            </li>
                            : <></>
                    }

                </ul>

                {/* Boton que controla el MAPA */}
                {
                    globalBotonMapa ?
                        <>

                            <div className="switch__menu">
                            </div>

                            <div className="switch__menu">
                                <input
                                    type="checkbox"
                                    className="switch__input"
                                    onClick={checkVistaMapa}
                                />
                                <div className="outer">
                                </div>
                                <div className="inner">
                                </div>
                                <div className="swith__textmapa">
                                    mapa
                    </div>
                            </div>
                        </>
                        : <></>
                }



                <form className="form-inline my-2 my-lg-0" onSubmit={submitChange}>
                    <input
                        className="form-control mr-sm-2 buscador"
                        type="text"
                        placeholder="Buscar: producto, precio, stock"
                        name="producto_buscado"
                        value={busquedaProducto.producto_buscado}
                        onChange={handleChange}
                    />
                    <button
                        className="btn btn-outline-success my-2 my-sm-0"
                        type="submit"
                    >Buscar</button>
                </form>
            </div>

        </nav>
    )
}

export default TiendaHeader
