import React, { useContext, useEffect, useState } from 'react'
import TiendaContext from '../../../context/tienda/tiendaContext';
import { getCategoriasMain } from '../../../services/categoriasServices';

const TiendaNavigationsCategoria = () => {

    const { seleccionarGlobalCategoriaMain } = useContext(TiendaContext);

    const [CategoriasMain, setCategoriasMain] = useState([]);

    useEffect(() => {
        getCategoriasMain().then((data) => {
            setCategoriasMain(data);
        });

    }, []);

    const seleccionaCategoriasYProductos = (objCategorias) => {
        seleccionarGlobalCategoriaMain(objCategorias);
                
    }

    return (
        <nav className="menu">

            <ul className="menu__lista">
                {
                    CategoriasMain ?
                        CategoriasMain.map((objCateoriaMain) => {
                            return (
                                <li 
                                    key={objCateoriaMain.pk}
                                    onClick={()=>seleccionaCategoriasYProductos(objCateoriaMain.category)}
                                >
                                    <img src={objCateoriaMain.image} alt="" />
                                    <span className="badge badge-danger badge-pill">{objCateoriaMain.category.length}</span>
                                    <span>{objCateoriaMain.name}</span>
                                </li>
                            )
                        })
                        : (<span>Cargando...</span>)
                }

            </ul>

        </nav>

    )
}

export default TiendaNavigationsCategoria
