import React from 'react'
import AuthState from './context/auth/authState'
import RouterMain from './routers/RouterMain'
import './index.css';

const App = () => {


  const authenticate = ()=> {
    return new Promise(resolve => setTimeout(resolve, 100)) // 2 seconds
  }

  const componentDidMount = () =>{
    authenticate().then(() => {
      const ele = document.getElementById('ipl-progress-indicator')
      // console.log("will");
      // console.log(ele);
      if(ele){
        ele.classList.add('available')
        // setTimeout(() => {
        //   // remove from DOM
        //   ele.outerHTML = ''
        // }, 2000)
      }
    })
  }
  componentDidMount();

  return (
    <>
          <AuthState>
            <RouterMain></RouterMain>
     
          </AuthState>

    </>
  );

}

export default App
