import React from 'react'
import { Switch, Route } from 'react-router-dom'
import TiendaState from '../context/tienda/tiendaState'
import TiendaHeader from '../modules/tienda/components/TiendaHeader'
import TiendaIndicadores from '../modules/tienda/components/TiendaIndicadores'
import TiendaNavigations from '../modules/tienda/components/TiendaNavigations'
import TiendaNavigationsDetail from '../modules/tienda/components/TiendaNavigationsDetail'
import TiendaOfertas from '../modules/tienda/components/TiendaOfertas'
import Tienda from '../modules/tienda/screens/Tienda'
import "./../modules/tienda/styles/tienda.css"
import RutaPrivada from './RutaPrivada'

const RouterTienda = () => {

    return (
        <TiendaState>
            <section className="fanel_pedidos container-fluid">
                <div className="row">
                    <div className="fanel_tienda col-sm-12">

                        <TiendaHeader />
                        <main className="tienda_container">

                            <Switch>
                                <RutaPrivada path={"/tienda/indicadores"} component={TiendaIndicadores} />
                                <Route path={"/tienda/ofertas"} component={TiendaOfertas} />
                                <Route path={"/tienda/pedidos"} component={Tienda} />
                                <Route path={"/"} component={Tienda} />

                            </Switch>
                        </main>
                    </div>
                    <div className="fanel_panelcontrol2 ">

                        <TiendaNavigationsDetail />
                    </div>
                    <div className="fanel_panelcontrol ">
                        <TiendaNavigations />
                    </div>

                </div>
            </section>
        </TiendaState>

    )
}

export default RouterTienda
