import React, { useEffect, useState } from 'react'
import { useContext } from 'react';
import { Carousel } from 'react-bootstrap'
import AuthContext from '../../../context/auth/authContext'
import { obtenerPedidosPagadosByUser } from '../../../services/pedidosServices';
import moment from 'moment'

const TiendaTracking = () => {

    const { user } = useContext(AuthContext);
    const [pedidosUsuario, setPedidosUsuario] = useState([]);

    useEffect(() => {
        if (user) {
            obtenerPedidosPagadosByUser(user).then((rpta) => {
                console.log("pedidos usuario");
                console.log(rpta);
                if (rpta) {
                    setPedidosUsuario(rpta)
                }
            });
        }
    }, [user]);

    return (
        <>
            <div className="card__controlaltura">

                <div className="card card__tracking">
                    <div className="card-header">
                        <strong>Tracking:</strong>
                    </div>

                    <Carousel>
                        {
                            !pedidosUsuario ? <h3>No hay pedidos pendientes...</h3>
                                :
                                pedidosUsuario.map((objPedidoU, i) => {
                                    // moment().format("YYYYMMDDhhmmss")
                                    return (
                                        <Carousel.Item key={objPedidoU.pk+moment()}>

                                            <h5 className="text-center mt-4 mb-0">Pedido # {objPedidoU.pk}</h5>
                                            <img
                                                className="d-block  carousel_img"
                                                src="https://res.cloudinary.com/dqcdixbty/image/upload/v1607915120/tcovid/transparente_z0ky9u.png"
                                                alt="Pedido #"
                                            />
                                            <Carousel.Caption>
                                                <div className="contanier">
                                                    <div className="row justify-content-center text-center">
                                                        <div className="col-md-8 mt-0 tracking_text">
                                                            <div>Pagando<br /><small>{objPedidoU.dateorder}</small></div>
                                                        </div>
                                                        <div className="col-md-4 mt-0">
                                                            <div className="tracking__estado">
                                                                <img src="https://res.cloudinary.com/dqcdixbty/image/upload/v1607885839/tcovid/check_fqmwyw.svg" className="tracking__img" alt="Perfil" />
                                                            </div>
                                                            <div className="trackin__linea_abajo"></div>
                                                        </div>
                                                        <div className="col-md-8 mt-4 tracking_text">
                                                            <div><strong>Preparando</strong><br /><small></small></div>
                                                        </div>
                                                        <div className="col-md-4 mt-4">
                                                            <div className="tracking__estado">...</div>
                                                            <div className="trackin__linea_abajo"></div>
                                                        </div>
                                                        <div className="col-md-8 mt-4 tracking_text">
                                                            <div>En ruta<br /><small></small></div>
                                                        </div>
                                                        <div className="col-md-4 mt-4">
                                                            <div className="tracking__estado"></div>
                                                            <div className="trackin__linea_abajo"></div>
                                                        </div>
                                                        <div className="col-md-8 mt-4 tracking_text">
                                                            <div>Entregado<br /><small>Aprox: {objPedidoU.datedelivery}</small></div>
                                                        </div>
                                                        <div className="col-md-4 mt-4 mb-4">
                                                            <div className="tracking__estado"></div>
                                                        </div>
                                                    </div>

                                                </div>
                                            </Carousel.Caption>
                                        </Carousel.Item>


                                    )
                                })

                        }


                    </Carousel>

                    <div className="card-footer">

                    </div>
                </div>

            </div>
        </>
    )
}

export default TiendaTracking
