import React, { useContext } from 'react'
import TiendaContext from '../../../context/tienda/tiendaContext'
import TiendaCategorias from '../components/TiendaCategorias'
import TiendaNavigationsCategoria from '../components/TiendaNavigationsCategoria'
import TiendaProductos from '../components/TiendaProductos'
import TiendaUbicaciones from '../components/TiendaUbicaciones'

const Tienda = () => {

    const { globalZonaVentaMapa } = useContext(TiendaContext);


    return (
        <>
            <div className="fanel_panelcontrol_izq ">
                <TiendaNavigationsCategoria />
            </div>
            <section className="zonaventa">
                <section className="zonaventa__tabla container-fluid">
                    <TiendaCategorias />

                    <div className="zonaventa__pedido">
                        {
                            globalZonaVentaMapa ?
                                <>
                                    <TiendaProductos />
                                    <TiendaUbicaciones />
                                </>
                                :
                                <>
                                    <TiendaUbicaciones />
                                    <TiendaProductos />
                                </>
                        }

                    </div>
                </section>
            </section>

        </>
    )
}

export default Tienda
