import { URL_BACKEND_LOCAL } from "../enviroment/enviroment";

export const registrarPedido =  async(objPedido) => {
    const response = await fetch(`${URL_BACKEND_LOCAL}/api/orders/`,{
        method: "POST",
        headers: {
            'Content-type': 'application/json'
        },
        body: JSON.stringify(objPedido), 
    });
    
    const json = await response.json();
    return json;
}


