export const obtenerPedidosPagadosByUser = async(id) => {
    const response = await fetch(`${URL_BACKEND_LOCAL}/pedidos_cliente/${id}/`);
    const json = await response.json();
    const json2 = json.filter((objPedidoUsu)=>objPedidoUsu.state==='pendiente')
    return json2;
};
