import { URL_BACKEND_LOCAL } from "../enviroment/enviroment"

export const getCategoriasMain = async () => {
    const response = await fetch(`${URL_BACKEND_LOCAL}/List_MainCategoryCategory_Products/`);
    const json = await response.json();
    return json;

}

export const getCategoriasTotal = async () => {
    const response = await fetch(`${URL_BACKEND_LOCAL}/List_Category_Products/`);
    const json = await response.json();
    return json;
}