import { useEffect, useReducer } from "react";
import TiendaContext from "./tiendaContext";
import TiendaReducer from "./tiendaReducer";

const TiendaState = (props) => {

    const [state, dispatch] = useReducer(TiendaReducer, {
        globalObjCategoriaMain: [],
        globalObjCategorias: [],
        globalObjModoEntrega: null,
        globalObjTipoProducto: null,
        globalMontoTotal: 0,
        globalMontoDescuentoTotal: 0,
        globalCheckDelivery:"",
        globalQTotal: 0,
        globalPedidos: [],
        globalZonaMenu: {
            perfil: -1,
            registro_usu: -1,
            olvide_pass: -1,
            carrito: -1,
            tracking: -1,
            chat: -1,
        },
        globalZonaVentaCarta: true,
        globalZonaVentaMapa: true,
        globalBotonMapa: true,

    });

    // A continuaciÃ³n la lista de funciones que modificara el estado

    const seleccionarGlobalCategoriaMain = (ObjCategoriaMain) => {
        dispatch({
            type: "SELECCIONAR_CATEGORIAMAIN",
            data: [...ObjCategoriaMain]
        });
    }

    const seleccionarCategoria = (ObjCategoria) => {
        dispatch({
            type: "SELECCIONAR_CATEGORIA",
            data: [...ObjCategoria]
        });
    }

    const modificarGlobalPedidos = (objPedidos) => {
        dispatch({
            type: "ACTUALIZAR_GLOBAL_PEDIDOS",
            data: [...objPedidos]
        })
    }

    const muestraGlobalMenu = (itemMenu) => {
        dispatch({
            type: "MUESTRA_MENU",
            data: { ...itemMenu }
        });
    }

    const muestraGlobalZonaVentaCarta = (itemCarta) => {
        dispatch({
            type: "MUESTRA_ZONAVENTA_CARTA",
            data: itemCarta
        })
    }
    const muestraGlobalZonaVentaMapa = (itemMapa) => {
        dispatch({
            type: "MUESTRA_ZONAVENTA_MAPA",
            data: itemMapa
        })
    }

    const muestraGlobalBotonMapa = (btnMapa) => {
        dispatch({
            type: "MUESTRA_BOTON_MAPA",
            data: btnMapa,
        })
    }

    const muestraGlobalMontoTotal = (montoTotal) => {
        dispatch({
            type: "MUESTRA_MONTO_TOTAL",
            data: montoTotal,
        })
    };


    const muestraGlobalMontoDescuentoTotal = (montoDescuentoTotal) => {
        dispatch({
            type: "MUESTRA_MONTO_DESCUENTO_TOTAL",
            data: montoDescuentoTotal,
        })
    };

    const muestraGlobalQTotal = (qTotal) => {
        dispatch({
            type: "MUESTRA_CANTIDAD_TOTAL",
            data: qTotal,
        })
    };

    const limpiarGlobalVariables = () => {
        dispatch({
            type: "LIMPIAR_VARIABLES",
            data: ""
        })
    };

    // LOCAL STORAGE:::
    let gPedidos = [];

    const iniciarPedidosLocalStorage = () => {
        gPedidos = localStorage.getItem('globalPedidos'); 
        if(gPedidos){
            dispatch({
                type: "ACTUALIZAR_GLOBAL_PEDIDOS",
                data: [...JSON.parse(gPedidos)]
            })
        } 

        if (localStorage.getItem('globalQTotal')) {
            muestraGlobalQTotal(localStorage.getItem('globalQTotal'));
        }

        if (localStorage.getItem('globalMontoTotal')) {
            muestraGlobalMontoTotal(localStorage.getItem('globalMontoTotal'));
        }

        if (localStorage.getItem('globalMontoDescuentoTotal')) {
            muestraGlobalMontoDescuentoTotal(localStorage.getItem('globalMontoDescuentoTotal'));
        }

    };

    useEffect(() => {
        iniciarPedidosLocalStorage();
    }, []);

    return (
        <TiendaContext.Provider
            value={{
                globalObjCategoriaMain: state.globalObjCategoriaMain,
                globalObjCategorias: state.globalObjCategorias,
                globalObjModoEntrega: state.globalObjModoEntrega,
                globalObjTipoProducto: state.globalObjTipoProducto,
                globalPedidos: state.globalPedidos,
                globalZonaMenu: state.globalZonaMenu,
                globalZonaVentaCarta: state.globalZonaVentaCarta,
                globalZonaVentaMapa: state.globalZonaVentaMapa,
                globalBotonMapa: state.globalBotonMapa,
                globalFormularioUsuario: state.globalFormularioUsuario,
                globalMontoTotal: state.globalMontoTotal,
                globalMontoDescuentoTotal: state.globalMontoDescuentoTotal,
                globalQTotal: state.globalQTotal,
                muestraGlobalMenu,
                muestraGlobalZonaVentaCarta,
                muestraGlobalZonaVentaMapa,
                muestraGlobalBotonMapa,
                seleccionarGlobalCategoriaMain,
                seleccionarCategoria,
                modificarGlobalPedidos,
                muestraGlobalMontoTotal,
                muestraGlobalMontoDescuentoTotal,
                muestraGlobalQTotal,
                limpiarGlobalVariables
            }}
        >
            {props.children}
        </TiendaContext.Provider>
    )

}

export default TiendaState;

