import { useEffect, useReducer } from "react";
import AuthContext from "./authContext";
import AuthReducer from "./authReducer";


const AuthState = (props) => {

    const [state, dispatch] = useReducer(AuthReducer, {
        autenticado: false,
        username: null,
        user: null,
        email: null,
        token: null,
        cargando: true,
        cell:null
    });

    // Aqui las funciones a compartir con los hijos

    useEffect(() => {
        iniciarSesionConLocalStorage();
    },[]);

    const iniciarSesionConLocalStorage = () => {
        
        if(localStorage.getItem('token') && localStorage.getItem('email') && localStorage.getItem('username') && localStorage.getItem('user') ){
            dispatch({
                type: "INICIAR_SESION",
                data: {
                    token: localStorage.getItem("token"),
                    email: localStorage.getItem("email"),
                    username: localStorage.getItem("username"),
                    user: localStorage.getItem("user")                }
            });
        }

    };

    const seleccionarUsurio = (objUsuario) => {
        dispatch({
            type:"SELECCIONAR_USUARIO",
            data: {...objUsuario}
        });
    }

    const inciarSesionX = (objLogueado) => {
        dispatch({
            type: "INICIAR_SESION",
            data: {...objLogueado}
        })
    }


    const cerrarSesion = () => {
        dispatch({
            type: "CERRAR_SESION",

        })
    }

    return (
            <AuthContext.Provider
            value={{
                autenticado: state.autenticado,
                username: state.username,
                user: state.user,
                cargando: state.cargando,
                email: state.email,
                cerrarSesion,
                seleccionarUsurio,
                inciarSesionX,
            }}
        >
            {props.children}

        </AuthContext.Provider>


    )

}

export default AuthState;