import React, { useContext, useEffect, useState } from 'react';
import { Bar, Pie } from 'react-chartjs-2';
import TiendaContext from '../../../context/tienda/tiendaContext';
import { obtenerPedidosProductoResumen, obtenerPedidosDeliveryResumen, obtenerPedidosUsuarioResumen } from '../../../services/pedidosServices';

const TiendaIndicadores = () => {

    const { muestraGlobalBotonMapa } = useContext(TiendaContext);
    const [listaProductos, setListaProductos] = useState([]);
    const [listaQProductos, setListaQProductos] = useState([]);
    const [listaUsuarios, setListaUsuarios] = useState([]);
    const [listaQUsuarios, setListaQUsuarios] = useState([]);
    const [listaPedidosDelivery, setListaPedidosDelivery] = useState([]);
    const [listaQPedidosDelivery, setListaQPedidosDelivery] = useState([]);
    const [totalPedidos, setTotalPedidos] = useState(0);
    const [totalProductos, setTotalProductos] = useState(0);
    const [totalUsuarios, setTotalUsuarios] = useState(0);


    let pedidoProducto = () => {

        obtenerPedidosProductoResumen().then((objResumen) => {
            console.log("--WS nueva");
            console.log(objResumen);
            let listaP = [];
            let listaQ = [];
            let listaTProd = 0;
            if (objResumen.length > 0) {
                objResumen.forEach((objLista) => {
                    console.log("- item -");
                    console.log(objLista);
                    listaP.push(objLista.producto_name);
                    listaQ.push(objLista.cant_ped);
                    listaTProd += objLista.cant_ped;
                });
                setListaProductos(listaP);
                setListaQProductos(listaQ);
                setTotalProductos(listaTProd);
            }
        });

        obtenerPedidosUsuarioResumen().then((objResumen) => {
            console.log(objResumen);
            let listaC = [];
            let listaQ = [];
            let listaTPed = 0;
            
            if (objResumen.length > 0) {
                objResumen.forEach((objLista) => {
                    listaC.push(objLista.usu_name);
                    listaQ.push(objLista.cant_ped);
                    listaTPed+=objLista.cant_ped;
            
                });
                setListaUsuarios(listaC);
                setListaQUsuarios(listaQ);
                setTotalUsuarios(objResumen.length);
                setTotalPedidos(listaTPed);
            }
        });

        obtenerPedidosDeliveryResumen().then((objResumen) => {
            console.log(objResumen);
            let listaCM = [];
            let listaQ = [];
            if (objResumen.length > 0) {
                objResumen.forEach((objLista) => {
                    listaCM.push(objLista.delivery_name);
                    listaQ.push(objLista.cant_ped);
                });
                setListaPedidosDelivery(listaCM);
                setListaQPedidosDelivery(listaQ);
            }
        });

    };

    useEffect(() => {
        muestraGlobalBotonMapa(false);
        pedidoProducto();
    }, []);


    return (
        <div className="indicadores__principal container-fluid ">

            <div className="row mt-2 justify-content-center">
                <div className="col-md-8 indicador_grupo text-center">
                    <div className="indicador ">
                        Pedidos<br></br>
                        <strong># {totalPedidos}</strong>
                    </div>
                    <div className="indicador ">
                        Productos<br></br>
                        <strong># {totalProductos}</strong>
                    </div>
                    <div className="indicador ">
                        Usuarios<br></br>
                        <strong># {totalUsuarios}</strong>
                    </div>

                </div>

                <div className="col-md-10">
                    <div className="row">
                        <div className="col-md-8">
                            <Bar
                                width={600}
                                height={400}
                                data={{
                                    labels: listaProductos, // ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],
                                    datasets: [
                                        // {
                                        //     label: "Monto",
                                        //     data: listaMontosProductos, //[12, 19, 3, 5, 2, 3],
                                        //     borderWidth: 2,
                                        //     backgroundColor: '#614ea0',
                                        //     // borderColor: "#614ea0"
                                        // },
                                        {
                                            label: "Q",
                                            data: listaQProductos, //[12, 19, 3, 5, 2, 3],
                                            borderWidth: 2,
                                            backgroundColor: '#ff9c63',
                                            borderColor: "#614ea0"
                                        }
                                    ]
                                }}
                                options={{
                                    maintainAspectRadio: false,
                                    responsive: true,
                                    scales: {
                                        yAxes: [{
                                            ticks: {
                                                beginAtZero: true
                                            }
                                        }]
                                    },
                                    legend: {
                                        labels: {
                                            fontSize: 16,
                                        }
                                    },
                                    title: {
                                        display: true,
                                        text: 'Productos mas demandados'
                                    }
                                }}
                            />
                        </div>
                        <div className="col-md-4">
                            <div className="row">
                            <div className="col">
                                <Pie
                                    width={600}
                                    height={400}
                                    data={{
                                        labels: listaPedidosDelivery, // ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],
                                        datasets: [
                                            // {
                                            //     label: "Monto",
                                            //     data: listaMontosCategoriasPrincipal, // [12, 19, 3, 5, 2, 3],
                                            //     borderWidth: 2,
                                            //     backgroundColor: '#614ea0',
                                            //     // borderColor: "#614ea0"
                                            // },
                                            {
                                                label: "Q",
                                                data: listaQPedidosDelivery, // [100, 90, 165, 234, 55, 121],
                                                borderWidth: 1,
                                                backgroundColor: "#614ea0",
                                                borderColor: "#ff9c63"
                                            }

                                        ]
                                    }}
                                    options={{
                                        maintainAspectRadio: false,
                                        responsive: true,
                                        legend: {
                                            labels: {
                                                fontSize: 16,
                                            }
                                        },
                                        title: {
                                            display: true,
                                            text: 'Pedidos por Repartidor'
                                        }
                                    }}
                                />
                            </div>
                            <div className="col">

                            </div>
                                <Bar
                                    width={400}
                                    height={300}
                                    data={{
                                        labels: listaUsuarios, // ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],
                                        datasets: [
                                            // {
                                            //     label: "Monto",
                                            //     data: listaMontosCategorias, // [12, 19, 3, 5, 2, 3],
                                            //     borderWidth: 2,
                                            //     backgroundColor: '#614ea0',
                                            //     // borderColor: "#614ea0"
                                            //     // borderColor: '614ea0'
                                            // },
                                            {
                                                label: "Q",
                                                data: listaQUsuarios, // [12, 19, 3, 5, 2, 3],
                                                borderWidth: 2,
                                                backgroundColor: '#ff9c63',
                                                borderColor: "#614ea0"
                                            }
                                        ]
                                    }}
                                    options={{
                                        maintainAspectRadio: false,
                                        responsive: true,
                                        scales: {
                                            yAxes: [{
                                                ticks: {
                                                    beginAtZero: true
                                                }
                                            }]
                                        },
                                        legend: {
                                            labels: {
                                                fontSize: 16,
                                            }
                                        },
                                        title: {
                                            display: true,
                                            text: 'Pedidos x Usuario'
                                        }
                                    }}
                                />
                            </div>

                        </div>

                    </div>

                </div>

            </div>
        </div>
    )
};

export default TiendaIndicadores
