import React, { useContext } from 'react'
import TiendaContext from '../../../context/tienda/tiendaContext';
import AuthContrasena from '../../auth/components/AuthContrasena';
import AuthLogin from '../../auth/components/AuthLogin';
import AuthRegister from '../../auth/components/AuthRegister';
import TiendaPanelControl from './TiendaPanelControl';
import TiendaTracking from './TiendaTracking';

const TiendaNavigationsDetail = () => {

    const { globalZonaMenu } = useContext(TiendaContext);

    return (
        <>
            <div >
                {
                    globalZonaMenu.perfil === 1 ? (
                        <AuthLogin />
                    ) : (<></>)
                }
                {
                    globalZonaMenu.registro_usu === 1 ? (
                        <AuthRegister />
                    ) : (<></>)
                }
                                {
                    globalZonaMenu.olvide_pass === 1 ? (
                        <AuthContrasena />
                    ) : (<></>)
                }
                {
                    globalZonaMenu.carrito === 1 ? (
                        <TiendaPanelControl />
                    ) : (<></>)
                }
                {
                    globalZonaMenu.tracking === 1 ? (
                        <TiendaTracking />
                    ) : (<></>)
                }

            </div>
        </>
    )
}

export default TiendaNavigationsDetail
