import React, { useContext } from 'react'
import TiendaContext from '../../../context/tienda/tiendaContext';

const TiendaPedidoProducto = ({ objPedidoUsu }) => {

    const { globalPedidos, modificarGlobalPedidos, globalMontoTotal, muestraGlobalMontoTotal, globalQTotal, muestraGlobalQTotal } = useContext(TiendaContext);

    const modificarProductos = (accion, objProd) => {

        let arreg = globalPedidos[0].orderdetail;
        let globalPedidosNuevo = [];
        let arreg_prod = arreg.find((objPedidoProducto) => objPedidoProducto.product === objProd.product)

        if (arreg_prod) {
            globalPedidosNuevo = [
                ...globalPedidos.map((objPedido) => {
                    objPedido.orderdetail.forEach((objPedidoProducto) => {
                        if (objPedidoProducto.product === objProd.product) {
                            if (accion === "incrementar") {
                                objPedidoProducto.quantity += 1;

                                muestraGlobalMontoTotal((globalMontoTotal - 0) + (objPedidoProducto.price_prod - 0));
                                muestraGlobalQTotal((globalQTotal - 0) + 1);
                                localStorage.setItem('globalMontoTotal', (globalMontoTotal - 0) + (objPedidoProducto.price_prod - 0));
                                localStorage.setItem('globalQTotal', (globalQTotal - 0) + 1);

                            } else {
                                if (objPedidoProducto.quantity > 0) {
                                    objPedidoProducto.quantity -= 1;
                                    muestraGlobalMontoTotal((globalMontoTotal - 0) - (objPedidoProducto.price_prod - 0));
                                    muestraGlobalQTotal((globalQTotal - 0) - 1);
                                    localStorage.setItem('globalMontoTotal', (globalMontoTotal - 0) - (objPedidoProducto.price_prod - 0));
                                    localStorage.setItem('globalQTotal', (globalQTotal - 0) - 1);
                                }
                            }
                        }
                    })
                    return objPedido;
                })
            ]

            globalPedidosNuevo = [...globalPedidosNuevo.map((objPedido) => {
                objPedido.orderdetail.filter((objPedidoProducto) => objPedidoProducto.quantity > 0)
                return objPedido;
            })];

            modificarGlobalPedidos(globalPedidosNuevo);
            localStorage.setItem('globalPedidos', JSON.stringify(globalPedidosNuevo));

        }
    };

    return (
        <div className="card-body">
            {
                objPedidoUsu.map((objPed) => {
                    if (objPed.quantity > 0) {
                        return (
                            <div className="row mb-1" key={objPed.product}>
                                <div className="col-md-8">
                                    <strong >{objPed.name_product}: </strong>
                                    <span className="badge badge-danger badge-pill">{objPed.quantity}</span>
                                    <small>{objPed.description_prod}</small>
                                </div>
                                <div className="col-md-4 d-flex justify-content-between">
                                    <p className="card_sumaped"
                                        onClick={() => {
                                            modificarProductos("decrementar", objPed)
                                        }}
                                    >-1</p>
                                    <strong><small> S/ {+objPed.price_prod} </small></strong>
                                    <p className="card_sumaped"
                                        onClick={() => {
                                            modificarProductos("incrementar", objPed)
                                        }}
                                    >+1</p>
                                </div>
                            </div>
                        )
                    }
                })
            }
        </div>

    )
}

export default TiendaPedidoProducto
