import { URL_BACKEND_LOCAL } from "../enviroment/enviroment";

export const getProductos = async () => {
    const response = await fetch(`${URL_BACKEND_LOCAL}/List_All_Products/`);
    const json = await response.json();
    return json;
}

export const getProductosBuscador = async (nombreBuscado) => {
    const response = await fetch(`${URL_BACKEND_LOCAL}/List_Product_search/${nombreBuscado}/`);
    const json = await response.json()
    return json;
}