
const AuthReducer = (stateActual, payload) => {
    switch (payload.type) {

        case "SELECCIONAR_USUARIO":
            return {
                ...stateActual,
                username: payload.data.username,
                email: payload.data.email,
                user: payload.data.user,
                cell: payload.data.cell
            }

        case "INICIAR_SESION":
            return {
                ...stateActual,
                autenticado: true,
                user: payload.data.user,
                email: payload.data.email,
                token: payload.data.token,
                username: payload.data.username,
                cargando: false,
            }
        case "CERRAR_SESION":
            localStorage.removeItem("token");
            localStorage.removeItem("username");
            localStorage.removeItem("user");
            localStorage.removeItem("email");
            localStorage.removeItem("globalMontoTotal")
            localStorage.removeItem("globalQTotal")
            localStorage.removeItem("globalPedidos")
            localStorage.removeItem("globalMontoDescuentoTotal")
            return {
                autenticado: false,
                username: null,
                user: null,
                token: null,
                cargando: true,
                email: null,
                cell:null
            }

        default:
            return { ...stateActual }
    }

}

export default AuthReducer;