import React, { useContext, useEffect, useState } from 'react'
import TiendaContext from '../../../context/tienda/tiendaContext'
import { getCategoriasTotal } from '../../../services/categoriasServices';

const TiendaCategorias = () => {

    const { globalObjCategoriaMain, seleccionarCategoria, seleccionarGlobalCategoriaMain } = useContext(TiendaContext);
    const [categoriaFiltrada, setCategoriaFiltrada] = useState([]);


    const muestraCategorias = async () => {
        getCategoriasTotal().then((rpta) => {
            seleccionarGlobalCategoriaMain(rpta);
        })

    };

    useEffect(() => {
        muestraCategorias();
    }, []);


    const changeCategory = (objCategoria) => {

        let check = document.getElementById(objCategoria.pk)
        let filtradoProductosTemporal2 = [];

        check.addEventListener('change', function () {

            // Si esta con check activo agregago productos
            if (this.checked) {
                console.log("check");
                setCategoriaFiltrada([...categoriaFiltrada].concat(objCategoria.product));
                seleccionarCategoria([...categoriaFiltrada].concat(objCategoria.product))
            } else {
                // Si se desactiva el check quito los producto
                filtradoProductosTemporal2 = [...categoriaFiltrada];

                objCategoria.product.forEach((objProd) => {
                    filtradoProductosTemporal2 = filtradoProductosTemporal2.filter((objProductTemporal) => objProductTemporal.pk !== objProd.pk)
                });                
                seleccionarCategoria(filtradoProductosTemporal2)
                setCategoriaFiltrada(filtradoProductosTemporal2);
                
            }
        });

    }

    return (
        <div className="container-fluid">
            <div className="row mb-2">

                <div className="container-fluid">

                    <ul className="categorias_lista">
                        {
                            globalObjCategoriaMain ?
                                globalObjCategoriaMain.map((ObjCategoria) => {
                                    return (
                                        < li key={ObjCategoria.pk}
                                        >
                                            <input type="checkbox"
                                                id={ObjCategoria.pk}
                                                onChange={() => {
                                                    changeCategory(ObjCategoria)
                                                }}
                                            />
                                            <label htmlFor={ObjCategoria.pk}>{ObjCategoria.name}
                                                <span className="badge badge-danger badge-pill ">{ObjCategoria.product.length}</span>
                                            </label>
                                        </li>
                                    )
                                })
                                :
                                <span>Selecciona alguna categoria...</span>
                        }

                    </ul>
                </div>

            </div>

        </div >
    )
}

export default TiendaCategorias
