import {  URL_BACKEND_LOCAL } from "../enviroment/enviroment"


export const crearUsuario = async (objUsu) => {
  const response = await fetch(`${URL_BACKEND_LOCAL}/api/users/signup/`, {
    method: "POST",
    headers: {
      "Content-type": "application/json",
    },
    body: JSON.stringify(objUsu),
  });
  const json = await response.json();
  return json;

};

export const tiendaLogin = async (email, password) => {
  const response = await fetch(`${URL_BACKEND_LOCAL}/api/users/login/`, {
    method: "POST",
    headers: {
      "Content-type": "application/json",
    },
    body: JSON.stringify({
      email: email,
      password: password,
    }),
  });
  const json = await response.json();
  return json;
};

export const tiendaLogout = async () => {
  const response = await fetch(`${URL_BACKEND_LOCAL}/logout/`);
  const json = await response.json();
  return json;

}


