import React, { useContext, useState } from 'react'
import AuthContext from '../../../context/auth/authContext';
import TiendaContext from '../../../context/tienda/tiendaContext';
import { crearUsuario } from '../../../services/auth';
import Swal from 'sweetalert2';

const AuthRegister = () => {

    const { seleccionarUsurio } = useContext(AuthContext);
    const { muestraGlobalMenu } = useContext(TiendaContext)

    const [formulario, setFormulario] = useState({
        email: "",
        username: "",
        photo: null,
        city: "Lima",
        country: "PerÃº",
        cell: null,
        password: "",
        password_confirmation: "",
        groups: 3,// Perfiles:  cliente final = group 3 | anonymous = 2

    });

    const handleChange = (e) => {
        setFormulario({
            ...formulario,
            [e.target.name]: e.target.value,
        });
    };

    const submitChange = (e) => {
        e.preventDefault();

        if (formulario) {
            crearUsuario(formulario).then((objUsu) => {
                console.log(objUsu);
                if (objUsu.password) {
                    seleccionarUsurio({
                        username: formulario.username,
                        email: formulario.email,
                        cell: formulario.cell
                    })

                    // blanquea variables
                    setFormulario({
                        email: "",
                        username: "",
                        photo: null,
                        city: "Lima",
                        country: "PerÃº",
                        cell: null,
                        password: "",
                        password_confirmation: "",
                        groups: 3
                    });
                    console.log("usuario creado con Ã©xito");
                    Swal.fire({
                        icon: "success",
                        title: "Usuario Creado",
                        text: "Usuario creado exitosamente!!!",
                        timer: 1500,
                        position: "top",
                        showConfirmButton: false
                    });

                }else{
                    Swal.fire({
                        icon: "error",
                        title: "Error",
                        text: "Ups hubo un error, intente nuevamente...",
                        timer:1500,
                        position: "top",
                        showConfirmButton: true
                    });
                }
            });
        } else {
            console.log("no hay formulario");
        }

    };

    return (
        <main className="login">
            <div className="login__form">
                <h2>Registro</h2>
                <form className="formulario" onSubmit={submitChange}>
                    <label htmlFor="">Nombre:</label>
                    <input
                        type="text"
                        className="formulario__input"
                        placeholder="Nombre"
                        name="username"
                        value={formulario.username}
                        onChange={handleChange}
                    />
                    <label htmlFor="">Email:</label>
                    <input
                        type="email"
                        className="formulario__input"
                        placeholder="Email"
                        name="email"
                        value={formulario.email}
                        onChange={handleChange}
                    />
                    <label htmlFor="">Password:</label>
                    <input
                        type="password"
                        className="formulario__input"
                        placeholder="Password"
                        name="password"
                        value={formulario.password}
                        onChange={handleChange}
                    />
                    <label htmlFor="">Repite Password:</label>
                    <input
                        type="password"
                        className="formulario__input"
                        placeholder="Password"
                        name="password_confirmation"
                        value={formulario.password_confirmation}
                        onChange={handleChange}
                    />

                    <button className="formulario__submit" type="submit">
                        Registrarse
                    </button>

                </form>
                <div >
                    <p
                        className="login__contrasena"
                        onClick={() => muestraGlobalMenu({
                            perfil: 1,
                            registro_usu: -1,
                            olvide_pass: -1,
                            carrito: -1,
                            tracking: -1,
                            chat: -1,
                        })}
                    >Login</p>
                </div>
                <div >
                    <p
                        className="login__contrasena"
                        onClick={() => muestraGlobalMenu({
                            perfil: -1,
                            registro_usu: -1,
                            olvide_pass: 1,
                            carrito: -1,
                            tracking: -1,
                            chat: -1,
                        })}
                    >Olvide contraseÃ±a</p>
                </div>
            </div>
        </main>

    )
}

export default AuthRegister
