import React, { useContext, useState } from 'react'
import AuthContext from '../../../context/auth/authContext';
import TiendaContext from '../../../context/tienda/tiendaContext'
import TiendaPaypal from './TiendaPaypal';
import TiendaPedidoProducto from './TiendaPedidoProducto';

const TiendaPanelControl = () => {

    const { autenticado } = useContext(AuthContext);
    const { globalPedidos, globalMontoTotal, muestraGlobalMenu, globalZonaMenu, muestraGlobalMontoDescuentoTotal, globalMontoDescuentoTotal, globalCheckDelivery } = useContext(TiendaContext);
    const [checkout, setCheckout] = useState(false);
  
    // console.log(moment(moment().toDate()).format("YYYYMMDDhhmmss")+moment().milliseconds());
    // console.log(moment().milliseconds());

    const escogeDelivery = (objPedidosUsu) =>{
        console.log(objPedidosUsu);

        let check = document.getElementById(objPedidosUsu.dateorder)
        
        check.addEventListener('change', function () {
            if (this.checked) {
                console.log("check");
                localStorage.setItem('globalMontoDescuentoTotal',objPedidosUsu.delivery_cost)
                muestraGlobalMontoDescuentoTotal(objPedidosUsu.delivery_cost);
            }else{
                console.log("Uncheck");
                muestraGlobalMontoDescuentoTotal(0);
                localStorage.setItem('globalMontoDescuentoTotal',0)
            };
        });

    };


    return (
        <>
            <div className="card__controlaltura">
                <div className="card card__controlPago">
                    <div className="card-header">
                        Pedidos:
                    </div>
                    {
                        !globalPedidos ? <div>No hay pedidos...</div> :
                            globalPedidos.map((objPedidosUsu) => {
                                console.log(globalCheckDelivery);
                                return (
                                    <div key={objPedidosUsu.dateorder}>
                                        <TiendaPedidoProducto
                                            objPedidoUsu={objPedidosUsu.orderdetail}

                                        />
                                        <div className="card-footer">
                                            total: 
                                            <strong>S/. {(globalMontoTotal-0)+(globalMontoDescuentoTotal-0)}</strong>
                                            <small> (<strong>
                                                {
                                                    globalMontoDescuentoTotal>0 ? <>SI</>: <>NO</>
                                                }
                                                </strong> incluye delivery)
                                            </small>

                                            <div className="card__delivery float-left mb-1 mr-3">
                                                <input 
                                                    className="card__delivery_input"
                                                    type="checkbox"
                                                    id={objPedidosUsu.dateorder} 
                                                    onClick={()=>escogeDelivery(objPedidosUsu)}
                                                />
                                                <label htmlFor={objPedidosUsu.dateorder} >
                                                    <span
                                                        className={`${globalMontoDescuentoTotal>0 && "card__delivery_labelspan"}`}
                                                    ></span>
                                                </label>
                                                <label htmlFor={objPedidosUsu.dateorder}>
                                                    <img src="https://res.cloudinary.com/dqcdixbty/image/upload/v1608517163/tcovid/delivery_2_n54ykh.svg" alt="imagen de delivery" />
                                                </label>
                                            </div>

                                            {
                                                checkout ? (
                                                    <TiendaPaypal></TiendaPaypal>
                                                ) : (
                                                        autenticado ? (

                                                            <button className="btn btn-danger float-right mb-1" onClick={() => {
                                                                setCheckout(true)
                                                            }}>
                                                                Pagar
                                                            </button>
                                                        ) : (
                                                                <button
                                                                    className="btn btn-danger float-right mb-1"
                                                                    onClick={() => muestraGlobalMenu({
                                                                        perfil: globalZonaMenu.perfil * -1,
                                                                        registro_usu: -1,
                                                                        olvide_pass: -1,
                                                                        carrito: -1,
                                                                        tracking: -1,
                                                                        chat: -1,
                                                                    })}
                                                                >
                                                                    Login
                                                                </button>
                                                            )

                                                    )
                                            }

                                        </div>

                                    </div>
                                )
                            })

                    }
                </div>



            </div>
        </>
    )
}

export default TiendaPanelControl
