import React, { useContext, useState } from 'react'
import TiendaContext from '../../../context/tienda/tiendaContext';

const AuthContrasena = () => {

    const { muestraGlobalMenu } = useContext(TiendaContext);

    const [formulario, setFormulario] = useState({
        nombre: "",
        email: "",
    });


    const onSubmit = () => {
        console.log("onSubmit");
    }

    const handleChange = () => {
        console.log("HandleChange");
    }

    return (
        <main className="login">
            <div className="login__form">
                <h2>Olvide ContraseÃ±a</h2>
                <form className="formulario" onSubmit={onSubmit}>
                    <label htmlFor="">Nombre:</label>
                    <input
                        type="text"
                        className="formulario__input"
                        placeholder="Nombre"
                        name="nombre"
                        value={formulario.nombre}
                        onChange={handleChange}
                    />
                    <label htmlFor="">Email:</label>
                    <input
                        type="email"
                        className="formulario__input"
                        placeholder="Email"
                        name="email"
                        value={formulario.email}
                        onChange={handleChange}
                    />

                    <button className="formulario__submit" type="submit">
                        Enviar correo
                    </button>

                </form>
                <div >
                    <p
                        className="login__contrasena"
                        onClick={() => muestraGlobalMenu({
                            perfil: 1,
                            registro_usu: -1,
                            olvide_pass: -1,
                            carrito: -1,
                            tracking: -1,
                            chat: -1,
                        })}
                    >Login</p>
                </div>
                <div >
                    <p 
                        className="login__contrasena" 
                        onClick={()=>muestraGlobalMenu({
                            perfil: -1,
                            registro_usu: 1,
                            olvide_pass: -1,
                            carrito: -1,
                            tracking: -1,
                            chat: -1,
                        })}
                    >Registrate</p>
                </div>
            </div>
        </main>
    )
}

export default AuthContrasena
