import React, { useEffect, useState } from 'react'
import { Map, Marker, Popup, TileLayer, withLeaflet } from 'react-leaflet';
import L from 'leaflet';
import leafGreen from './assets/img/nurse.svg';
import leafMiUbicacion from './../img/mi_ubicacion.svg';
import { SearchControl, OpenStreetMapProvider } from 'react-leaflet-geosearch';
import { getTiendaProductos } from '../../../services/storeServices';
import { Carousel } from 'react-bootstrap'
import { useContext } from 'react';
import moment from "moment";
import TiendaContext from '../../../context/tienda/tiendaContext';
import AuthContext from '../../../context/auth/authContext';

const TiendaMapa = () => {

    const { email } = useContext(AuthContext);
    const { globalObjCategorias, seleccionarCategoria, globalPedidos, modificarGlobalPedidos, globalMontoTotal, muestraGlobalMontoTotal, globalQTotal, muestraGlobalQTotal } = useContext(TiendaContext);
    const [tiendasProductos, setTiendasProductos] = useState([]);

    const prov = OpenStreetMapProvider();
    const GeoSearchControlElement = withLeaflet(SearchControl);

    // Obtengo la ubicaciÃ³n del cliente
    const [ubicacionUser, setUbicacionUser] = useState({
        latitude: 0,
        longitude: 0,
    });

    // Obtener las tiendas y sus productos desde la WS:
    const obtenerTiendasyProductos = () => {
        getTiendaProductos().then((objTiendasProductos) => {
            console.log("ffffff");
            console.log(objTiendasProductos);
            setTiendasProductos(objTiendasProductos);
        });
    };

    useEffect(() => {
        obtenerTiendasyProductos();

        // Obtener ubicaciÃ³n actual
        navigator.geolocation.getCurrentPosition(
            function (position) {
                setUbicacionUser({
                    latitude: position.coords.latitude,
                    longitude: position.coords.longitude,
                })
            },
            function (error) { console.log(error) },
            {
                enableHighAccuracy: true
            }
        );

    }, []);

    let iconClinica = L.icon({
        iconUrl: leafGreen,
        // shadowUrl: leafGreen,
        iconSize: [78, 55], // size of the icon
        shadowSize: [50, 64], // size of the shadow
        iconAnchor: [22, 94], // point of the icon which will correspond to marker's location
        shadowAnchor: [4, 62],  // the same for the shadow
        popupAnchor: [-3, -76]

    });

    let iconUser = L.icon({
        iconUrl: leafMiUbicacion,
        // shadowUrl: leafGreen,
        iconSize: [78, 55], // size of the icon
        shadowSize: [50, 64], // size of the shadow
        iconAnchor: [22, 94], // point of the icon which will correspond to marker's location
        shadowAnchor: [4, 62],  // the same for the shadow
        popupAnchor: [-3, -76]

    });


    const actualizaQProducto = (pkProd) => {
        if (globalPedidos.length > 0) {
            // Busco si existe un cliente logueado para buscar si tiene productos previamente listados
            let pedidoDetalle = globalPedidos[0].orderdetail.find((objPedidoDetalle) => objPedidoDetalle.product === pkProd);
            if (pedidoDetalle) {
                return pedidoDetalle.quantity;
            }
        }
        return 0;
    }

    // modificar el listao de pedidos y sus cantidades
    const modificarProductos = (accion, objProd) => {
        // Valido si existen pedidos globales

        let globalPedidosNuevo = [];
        if (globalPedidos.length > 0) {
            // Si hay al menos un pedido

            // Si el producto escogido ya existe se incrementa el contador:
            let arreg = globalPedidos[0].orderdetail;

            let arreg_prod = arreg.find((objPedidoProducto) => objPedidoProducto.product === objProd.pk)
            if (arreg_prod) {
                // Incremento la cantidad
                globalPedidosNuevo = [
                    ...globalPedidos.map((objPedido) => {
                        objPedido.orderdetail.forEach((objPedidoProducto) => {
                            if (objPedidoProducto.product === objProd.pk) {
                                if (accion === "incrementar") {
                                    objPedidoProducto.quantity += 1;
                                    muestraGlobalMontoTotal((globalMontoTotal - 0) + (objPedidoProducto.price_prod - 0));
                                    localStorage.setItem('globalMontoTotal', (globalMontoTotal - 0) + (objPedidoProducto.price_prod - 0));
                                    muestraGlobalQTotal((globalQTotal - 0) + 1);
                                    localStorage.setItem('globalQTotal', (globalQTotal - 0) + 1);
                                } else {
                                    if (objPedidoProducto.quantity > 0) {
                                        objPedidoProducto.quantity -= 1;
                                        muestraGlobalMontoTotal((globalMontoTotal - 0) - (objPedidoProducto.price_prod - 0));
                                        muestraGlobalQTotal((globalQTotal - 0) - 1);
                                        localStorage.setItem('globalMontoTotal', (globalMontoTotal - 0) - (objPedidoProducto.price_prod - 0));
                                        localStorage.setItem('globalQTotal', (globalQTotal - 0) - 1);
                                    }
                                }
                            }
                        })
                        return objPedido;
                    })
                ]

                globalPedidosNuevo = [...globalPedidosNuevo.map((objPedido) => {
                    objPedido.orderdetail.filter((objPedidoProducto) => objPedidoProducto.quantity > 0)
                    return objPedido;
                })]


            } else {
                // Si el producto escogido es nuevo se incluye en la lista:            
                arreg.push({
                    product: objProd.pk,
                    store: objProd.store,
                    name_product: objProd.name,
                    image_product: objProd.image,
                    quantity: 1,
                    discount: "0.00",
                    description_prod: objProd.description,
                    price_prod: objProd.price
                });
                globalPedidosNuevo = [{
                    ...globalPedidos[0],
                    orderdetail: arreg,
                }]
                muestraGlobalMontoTotal((globalMontoTotal - 0) + (objProd.price - 0));
                muestraGlobalQTotal((globalQTotal - 0) + 1);
                localStorage.setItem('globalMontoTotal', (globalMontoTotal - 0) + (objProd.price - 0));
                localStorage.setItem('globalQTotal', (globalQTotal - 0) + 1);

            }

            modificarGlobalPedidos(globalPedidosNuevo);
            localStorage.setItem('globalPedidos', JSON.stringify(globalPedidosNuevo));

        } else {
            // no hay ningun pedido:
            if (accion === "incrementar") {

                globalPedidosNuevo = [
                    {
                        email_user: email ? email : "",
                        dateorder: moment().format("YYYY-MM-DD"), //"2020-11-30", "YYYY-MM-DD hh:mm:ss"
                        state: "pendiente",
                        delivery: true,
                        delivery_cost: "10.00",
                        total_cost: objProd.price,
                        deliveryman: 1,
                        name_deliveryman: "Chamito1", // falta crear un modulo para asignar automatico los entregables
                        orderdetail: [
                            {
                                product: objProd.pk,
                                store: objProd.store,
                                name_store: objProd.store_name,
                                name_product: objProd.name,
                                image_product: objProd.image,
                                quantity: 1,
                                discount: "0.00",
                                description_prod: objProd.description,
                                price_prod: objProd.price
                            }
                        ]
                    }
                ]

                modificarGlobalPedidos(globalPedidosNuevo);
                muestraGlobalMontoTotal((globalMontoTotal - 0) + (objProd.price - 0));
                muestraGlobalQTotal((globalQTotal - 0) + 1);
                localStorage.setItem('globalMontoTotal', objProd.price);
                localStorage.setItem('globalQTotal', 1);
                console.log(globalPedidosNuevo);
                localStorage.setItem('globalPedidos', JSON.stringify(globalPedidosNuevo));
            }
        }
    };



    return (
        <Map
            center={[ubicacionUser.latitude, ubicacionUser.longitude]}
            zoom={13}
            className="zona__mapa"
            ondblclick={(e) => {
                console.log(L.marker(e.latlng));
                console.log(e.latlng.lat);
                console.log(e.latlng.lng);
                setUbicacionUser({
                    latitude: e.latlng.lat,
                    longitude: e.latlng.lng,
                })
            }}
        >
            <TileLayer
                url="https://api.mapbox.com/styles/v1/pedromolina/ckhigjlv703y319s1cvxixe5q/tiles/256/{z}/{x}/{y}@2x?access_token=pk.eyJ1IjoicGVkcm9tb2xpbmEiLCJhIjoiY2tlcHRpOHRzMTRhMTJxbzl2bXBjZTZsbCJ9.XFHiwM8wfOYS2Vu6KrWWQA"
                attribution="&copy; <a href=&quot;http://osm.org/copyright&quot;>OpenStreetMap</a> contributors"
            />

            {/* <Polyline
                positions={rutasLima}
                color={"purple"}
                onclick={(e) => {
                    console.log("Alguien esta haciendo click");
                }}
            /> */}

            {/* Marca mi ubicaciÃ³n actual */}
            <Marker
                position={[ubicacionUser.latitude, ubicacionUser.longitude]}
                icon={iconUser}
            >

                {/* Buscador de direcciÃ³n */}
                <GeoSearchControlElement
                    provider={prov}
                    showMarker={false}
                    showPopup={false}
                    popupFormat={({ query, result }) => result.label}
                    maxMarkers={3}
                    retainZoomLevel={false}
                    animateZoom={true} autoClose={false}
                    searchLabel={'ejm: av.Brasil 2020, Jesus Maria, Lima'}
                    keepResult={true}
                />

                {/* Ubicaciones de los lugares de venta */}
            </Marker>
            {
                !tiendasProductos ? <></> :
                    tiendasProductos.map((objTienda) => {
                        return !objTienda ? <></> :
                            (
                                <Marker
                                    key={objTienda.pk}
                                    position={[objTienda.shop_lat, objTienda.shop_lon]}
                                    icon={iconClinica}
                                >
                                    <Popup>
                                        {/* Coloco un carousel de los productos de esta tienda */}
                                        <Carousel>
                                            {
                                                !objTienda ? <></> :
                                                    objTienda.productstore.map((objTiendaProducto) => {
                                                        // console.log("mapa-prod");
                                                        // console.log(objTiendaProducto);
                                                        return (!objTiendaProducto || objTiendaProducto.length <= 0) ? <></> :
                                                            (
                                                                <Carousel.Item key={objTienda.pk + objTiendaProducto.pk}>
                                                                    <img
                                                                        className="d-block  carousel_img_mapa"
                                                                        src="https://res.cloudinary.com/dqcdixbty/image/upload/v1607915120/tcovid/transparente_z0ky9u.png"
                                                                        alt="fondo transparente"
                                                                    />
                                                                    <Carousel.Caption >
                                                                        {/* Producto a ofrecer */}

                                                                        <div className="zonaventa__card">
                                                                            <div className="card__zonaSuperior">
                                                                                <img src={objTiendaProducto.image} alt="imgen de producto" />

                                                                                {
                                                                                    objTiendaProducto.offer * 100 === 0 ? <></> :
                                                                                        <p className="card__dcto">Dscto. {objTiendaProducto.offer * 100}%</p>
                                                                                }

                                                                                <p className="card__conteo">
                                                                                    <span>
                                                                                        {
                                                                                            !globalPedidos ? 0 :
                                                                                                actualizaQProducto(objTiendaProducto.pk) 
                                                                                        }
                                                                                    </span>
                                                                                    <img src="https://res.cloudinary.com/dqcdixbty/image/upload/v1608516952/tcovid/carrito_rrwbua.svg" height="10" alt="imgen de carrito compras" />
                                                                                </p>
                                                                                <p className="card__suma" onClick={() => {
                                                                                    modificarProductos("incrementar", objTiendaProducto)
                                                                                }}>+1</p>
                                                                                <p className="card__resta" onClick={() => {
                                                                                    modificarProductos("decrementar", objTiendaProducto)
                                                                                }}>-1</p>
                                                                    
                                                                                <p className="card__empresa">{objTiendaProducto.store_name}</p>
                                                                                <p className="card__producto">{objTiendaProducto.name}</p>
                                                                            </div>

                                                                            <div className="card__zonaInferior">

                                                                                <p className="zonaInferior_datos">{objTiendaProducto.store_hour_sem}</p>
                                                                                <p className="zonaInferior_datos">{objTiendaProducto.store_hour_fsem}</p>
                                                                                <div className="card__descripcion">
                                                                                    <img src="https://res.cloudinary.com/dqcdixbty/image/upload/v1608517163/tcovid/delivery_2_n54ykh.svg" alt="" height="20" /> Delivery
                                                                                </div>

                                                                                <p className="zonaInferior_datos card__subtitulo">Ver mas...</p>
                                                                                <p className="card__descripcion"><i className="far fa-building"></i>{objTiendaProducto.store_address}</p>

                                                                                <p className="card__precio">S/. {objTiendaProducto.price}</p>

                                                                            </div>
                                                                        </div>

                                                                    </Carousel.Caption>
                                                                </Carousel.Item>
                                                            )
                                                    })
                                            }
                                        </Carousel>
                                    </Popup>
                                </Marker>
                            )
                    })
            }
        </Map>
    )
}

export default TiendaMapa
