import React, { useContext, useState } from 'react'
import Swal from 'sweetalert2';
import AuthContext from '../../../context/auth/authContext';
import TiendaContext from '../../../context/tienda/tiendaContext';
import { tiendaLogin, tiendaLogout } from '../../../services/auth';
import { obtenerPedidosPagadosByUser } from '../../../services/pedidosServices';
import "./../styles/auth.css";

const AuthLogin = () => {

    const { inciarSesionX, autenticado, cerrarSesion, username } = useContext(AuthContext);
    const { muestraGlobalMenu, limpiarGlobalVariables } = useContext(TiendaContext);

    const obtienePedidosPagados = (user) =>{
        obtenerPedidosPagadosByUser(user).then((rpta)=>{
            if(rpta.length>0){
                console.log(rpta);
            }
        })
    };

    const [formulario, setFormulario] = useState({
        email: "",
        password: "",
    });

    const handleChange = (e) => {
        setFormulario({
            ...formulario,
            [e.target.name]: e.target.value,
        });
    };

    const onSubmit = (e) => {
        e.preventDefault();

        tiendaLogin(formulario.email, formulario.password).then((rpta) => {
            if (rpta.token) {
                console.log(rpta.token);
                inciarSesionX({
                    token: rpta.token,
                    email: rpta.user.email,
                    username: rpta.user.username,
                    user: rpta.user.pk
                });
                muestraGlobalMenu({
                    perfil: -1,
                    registro_usu: -1,
                    olvide_pass: -1,
                    carrito: -1,
                    tracking: -1,
                    chat: -1,
                })

                // Guardo datos en LOCAL STORAGE:
                localStorage.setItem('token',rpta.token);
                localStorage.setItem('email',rpta.user.email);
                localStorage.setItem('username',rpta.user.username);
                localStorage.setItem('user',rpta.user.pk);

                obtienePedidosPagados(rpta.user.pk);
            }

        });

    };

    const cierraSesion = () => {
        cerrarSesion();
        limpiarGlobalVariables();
        Swal.fire({
            icon:"success",
            title:"CerrÃ³ sesiÃ³n",
            position: "top",
            timer: 1500,
            showConfirmButton: false
        });
    };

    return (
        <main className="login">
            <div className="login__form">
                {
                    autenticado ?
                        <>
                        <h4 className="mt-2 ml-2">Hola <strong>{username}</strong>,</h4>
                        <h5 className="mt-3 ml-2">Puedes cerrar sesiÃ³n aqui:</h5>
                        <button
                            className="btn btn-danger mb-3 ml-2"
                            onClick={cierraSesion}
                        >Cerrar SesiÃ³n</button>
                        </>
                        : <>
                            <h2>Login</h2>
                            <form className="formulario" onSubmit={onSubmit}>
                                <label htmlFor="">Email:</label>
                                <input
                                    type="email"
                                    className="formulario__input"
                                    placeholder="Email"
                                    name="email"
                                    value={formulario.email}
                                    onChange={handleChange}
                                />
                                <label htmlFor="">Password:</label>
                                <input
                                    type="password"
                                    className="formulario__input"
                                    placeholder="Password"
                                    name="password"
                                    value={formulario.password}
                                    onChange={handleChange}
                                />
                                <button className="formulario__submit" type="submit">
                                    Iniciar SesiÃ³n
                                </button>

                            </form>
                            <div >
                                <p
                                    className="login__contrasena"
                                    onClick={() => muestraGlobalMenu({
                                        perfil: -1,
                                        registro_usu: -1,
                                        olvide_pass: 1,
                                        carrito: -1,
                                        tracking: -1,
                                        chat: -1,
                                    })}
                                >Â¿Olvidaste tu contraseÃ±a?</p>
                                <p
                                    className="login__contrasena"
                                    onClick={() => muestraGlobalMenu({
                                        perfil: -1,
                                        registro_usu: 1,
                                        olvide_pass: -1,
                                        carrito: -1,
                                        tracking: -1,
                                        chat: -1,
                                    })}
                                >Registrate</p>

                            </div>

                        </>

                }


            </div>
        </main>
    )
}

export default AuthLogin
