
const TiendaReducer = (stateActual, payload) => {

    switch (payload.type) {
        case "SELECCIONAR_CATEGORIAMAIN":
            return {
                ...stateActual,
                globalObjCategoriaMain: payload.data,
            }

        case "SELECCIONAR_CATEGORIA":
            return {
                ...stateActual,
                globalObjCategorias: payload.data,
            }

        case "ACTUALIZAR_GLOBAL_PEDIDOS":
            return {
                ...stateActual,
                globalPedidos: payload.data,

            }
        case "MUESTRA_MENU":
            return {
                ...stateActual,
                globalZonaMenu: payload.data,
            }
        case "MUESTRA_ZONAVENTA_CARTA":
            return {
                ...stateActual,
                globalZonaVentaCarta: payload.data,
            }
        case "MUESTRA_ZONAVENTA_MAPA":
            return {
                ...stateActual,
                globalZonaVentaMapa: payload.data,
            }
        case "MUESTRA_BOTON_MAPA":
            return {
                ...stateActual,
                globalBotonMapa: payload.data,
            }
        case "MUESTRA_MONTO_TOTAL":
            return {
                ...stateActual,
                globalMontoTotal: payload.data,
            }
        case "MUESTRA_MONTO_DESCUENTO_TOTAL":
            if(payload.data>0){
                return {
                    ...stateActual,
                    globalMontoDescuentoTotal: payload.data,
                    globalCheckDelivery: "checked"
                }
            }else{
                return {
                    ...stateActual,
                    globalMontoDescuentoTotal: payload.data,
                    globalCheckDelivery: ""
                }
            }
        case "MUESTRA_CANTIDAD_TOTAL":
            return {
                ...stateActual,
                globalQTotal: payload.data,
            }
        case "LIMPIAR_VARIABLES":
            return {
                ...stateActual,
                globalObjCategoriaMain: [],
                globalObjModoEntrega: null,
                globalObjTipoProducto: null,
                globalMontoTotal: 0,
                globalMontoDescuentoTotal: 0,
                globalCheckDelivery:"",
                globalQTotal: 0,
                globalPedidos: [],
                globalZonaMenu: {
                    perfil: -1,
                    registro_usu: -1,
                    olvide_pass: -1,
                    carrito: -1,
                    tracking: -1,
                    chat: -1,
                },
                globalZonaVentaCarta: true,
                globalZonaVentaMapa: true,
                globalBotonMapa: true
            }

        default:
            return { ...stateActual }
    }

}

export default TiendaReducer;