import React, { useContext, useEffect, useRef, useState } from 'react'
import TiendaContext from '../../../context/tienda/tiendaContext';
import { registrarPedido } from '../../../services/pedidosServices';
import Swal from 'sweetalert2';
import moment from 'moment'
import AuthContext from '../../../context/auth/authContext';

const TiendaPaypal = () => {

    const { user } = useContext(AuthContext);
    const { globalMontoTotal, globalMontoDescuentoTotal, globalPedidos, modificarGlobalPedidos, muestraGlobalMontoTotal, muestraGlobalQTotal, muestraGlobalMontoDescuentoTotal, muestraGlobalMenu } = useContext(TiendaContext);
    const [paidFor, setPaidFor] = useState(false);
    let detalleProductos = "";

    globalPedidos[0]?.orderdetail.forEach((objDetalle) => {
        if (objDetalle.quantity > 0) {
            detalleProductos = `${detalleProductos} // ${objDetalle.name_product} (${objDetalle.quantity})`;
        }
    })


    let globalPedidosFinal = [{
        ...globalPedidos[0],
        address: "Jr. Hernan Cortez 1091, El Agustino, Lima,PerÃº", // no implementado, pedir al Pagar
        total_cost: (globalMontoTotal-0)+(globalMontoDescuentoTotal-0),
        order_lat: "-12.5429900", // no implementado, pedir al pagar
        order_lon: "-74.6086300", // no implementado, pedir al pagar
        datedelivery: moment(moment().add(2, 'days').toDate()).format("YYYY-MM-DD"), // getdate + 2 dias
        user: user

    }]

    console.log("pedidos final");
    console.log(globalPedidosFinal);

    let paypal = useRef();

    useEffect(() => {
        window.paypal
            .Buttons({
                createOrder: (data, actions, err) => {
                    return actions.order.create({
                        intent: "CAPTURE",
                        purchase_units: [
                            {
                                description: detalleProductos,
                                amount: {
                                    currency_code: "USD",
                                    value: (globalMontoTotal-0)+(globalMontoDescuentoTotal-0),
                                },
                            },
                        ],
                    });
                },
                onApprove: async (data, actions) => {
                    console.log("inicio pago");
                    const order = await actions.order.capture();
                    setPaidFor(true);
                    console.log(order);
                    console.log("terminÃ³ el pago");

                    // aqui llamo al servicio y guarde en la BD
                    registrarPedido(globalPedidosFinal[0]).then((rpta) => {
                        console.log(rpta);
                        if (rpta) {
                            // luego limpio las variables globalesde pedidos, monto y cantidad 
                            modificarGlobalPedidos([]);
                            muestraGlobalQTotal(0);
                            muestraGlobalMontoTotal(0);
                            muestraGlobalMontoDescuentoTotal(0);
                            muestraGlobalMenu({
                                perfil: -1,
                                registro_usu: -1,
                                olvide_pass: -1,
                                carrito: -1,
                                tracking: -1,
                                chat: -1,
                            });

                            // limpio el local Storage
                            localStorage.removeItem('globalPedidos');
                            localStorage.removeItem('globalMontoTotal');
                            localStorage.removeItem('globalMontoDescuentoTotal');
                            localStorage.removeItem('globalQTotal');
                            localStorage.removeItem('globalPedidos');
                            localStorage.removeItem('__paypal_storage__');


                            Swal.fire({
                                title: "Pago Exitoso",
                                icon: "success",
                                text: "Pago realizado exitosamente!!!",
                                position: "top",
                                timer: 1500,
                                showConfirmButton: false,

                            });
                        }

                    })

                },
                onError: (err) => {
                    console.log(err);
                },
            })
            .render(paypal.current);

    }, [user]);

    return (
        <div>
            {
                paidFor ? (
                    <div>
                        <h3>Felicitaciones!!!</h3>
                    </div>
                ) : (
                        <div className="mt-4">
                            <div ref={paypal}></div>
                        </div>
                    )
            }
        </div>
    )
}

export default TiendaPaypal
