import React, { useContext, useEffect } from 'react'
import AuthContext from '../../../context/auth/authContext';
import TiendaContext from '../../../context/tienda/tiendaContext'
import { getProductos } from '../../../services/productServices';
import moment from "moment";

const TiendaProductos = () => {

    const { email } = useContext(AuthContext);
    const { globalObjCategorias, seleccionarCategoria, globalPedidos, modificarGlobalPedidos, globalMontoTotal, muestraGlobalMontoTotal, globalQTotal, muestraGlobalQTotal } = useContext(TiendaContext);


    // obtiene todos los productos y los actualiza en la variables globales de Categorias y productos.
    const traerProductos = async () => {
        getProductos().then((rpta) => {
            if (rpta.length) {
                seleccionarCategoria(rpta)
            }
        })
    }

    useEffect(() => {
        traerProductos();
    }, []);

    const actualizaQProducto = (pkProd) => {
        if (globalPedidos.length > 0) {
            // Busco si existe un cliente logueado para buscar si tiene productos previamente listados
            let pedidoDetalle = globalPedidos[0].orderdetail.find((objPedidoDetalle) => objPedidoDetalle.product === pkProd);
            if (pedidoDetalle) {
                return pedidoDetalle.quantity;
            }
        }
        return 0;
    }

    // modificar el listao de pedidos y sus cantidades
    const modificarProductos = (accion, objProd) => {

        let globalPedidosNuevo = [];
        if (globalPedidos.length > 0) {
            // Si hay al menos un pedido

            // Si el producto escogido ya existe se incrementa el contador:
            let arreg = globalPedidos[0].orderdetail;

            let arreg_prod = arreg.find((objPedidoProducto) => objPedidoProducto.product === objProd.pk)
            if (arreg_prod) {
                // Incremento la cantidad
                globalPedidosNuevo = [
                    ...globalPedidos.map((objPedido) => {
                        objPedido.orderdetail.forEach((objPedidoProducto) => {
                            if (objPedidoProducto.product === objProd.pk) {
                                if (accion === "incrementar") {
                                    objPedidoProducto.quantity += 1;
                                    muestraGlobalMontoTotal((globalMontoTotal - 0) + (objPedidoProducto.price_prod - 0));
                                    localStorage.setItem('globalMontoTotal', (globalMontoTotal - 0) + (objPedidoProducto.price_prod - 0));
                                    muestraGlobalQTotal((globalQTotal - 0) + 1);
                                    localStorage.setItem('globalQTotal', (globalQTotal - 0) + 1);
                                } else {
                                    if (objPedidoProducto.quantity > 0) {
                                        objPedidoProducto.quantity -= 1;
                                        muestraGlobalMontoTotal((globalMontoTotal - 0) - (objPedidoProducto.price_prod - 0));
                                        muestraGlobalQTotal((globalQTotal - 0) - 1);
                                        localStorage.setItem('globalMontoTotal', (globalMontoTotal - 0) - (objPedidoProducto.price_prod - 0));
                                        localStorage.setItem('globalQTotal', (globalQTotal - 0) - 1);
                                    }
                                }
                            }
                        })
                        return objPedido;
                    })
                ]

                globalPedidosNuevo = [...globalPedidosNuevo.map((objPedido) => {
                    objPedido.orderdetail.filter((objPedidoProducto) => objPedidoProducto.quantity > 0)
                    return objPedido;
                })]


            } else {
                // Si el producto escogido es nuevo se incluye en la lista:            
                arreg.push({
                    product: objProd.pk,
                    store: objProd.store,
                    name_product: objProd.name,
                    image_product: objProd.image,
                    quantity: 1,
                    discount: "0.00",
                    description_prod: objProd.description,
                    price_prod: objProd.price
                });
                globalPedidosNuevo = [{
                    ...globalPedidos[0],
                    orderdetail: arreg,
                }]
                muestraGlobalMontoTotal((globalMontoTotal - 0) + (objProd.price - 0));
                muestraGlobalQTotal((globalQTotal - 0) + 1);
                localStorage.setItem('globalMontoTotal', (globalMontoTotal - 0) + (objProd.price - 0));
                localStorage.setItem('globalQTotal', (globalQTotal - 0) + 1);

            }

            modificarGlobalPedidos(globalPedidosNuevo);
            localStorage.setItem('globalPedidos', JSON.stringify(globalPedidosNuevo));

        } else {
            // no hay ningun pedido:
            if (accion === "incrementar") {

                globalPedidosNuevo = [
                    {
                        email_user: email ? email : "",
                        dateorder: moment().format("YYYY-MM-DD"), //"2020-11-30", "YYYY-MM-DD hh:mm:ss"
                        state: "pendiente",
                        delivery: true,
                        delivery_cost: "10.00",
                        total_cost: objProd.price,
                        deliveryman: 1,
                        name_deliveryman: "Chamito1", // falta crear un modulo para asignar automatico los entregables
                        orderdetail: [
                            {
                                product: objProd.pk,
                                store: objProd.store,
                                name_store: objProd.store_name,
                                name_product: objProd.name,
                                image_product: objProd.image,
                                quantity: 1,
                                discount: "0.00",
                                description_prod: objProd.description,
                                price_prod: objProd.price
                            }
                        ]
                    }
                ]

                modificarGlobalPedidos(globalPedidosNuevo);
                muestraGlobalMontoTotal((globalMontoTotal - 0) + (objProd.price - 0));
                muestraGlobalQTotal((globalQTotal - 0) + 1);
                localStorage.setItem('globalMontoTotal', objProd.price);
                localStorage.setItem('globalQTotal', 1);
                console.log(globalPedidosNuevo);
                localStorage.setItem('globalPedidos', JSON.stringify(globalPedidosNuevo));
            }
        }


    };


    return (
        <div className="zonaventa__productos">
            <section className="zonaventa__principal">
                {
                    globalObjCategorias ?
                        globalObjCategorias.map((ObjProducto) => {
                            return (
                                <div className="zonaventa__card" key={ObjProducto.pk+moment().format("YYYYMMDDhhmmss")}>
                                    <div className="card__zonaSuperior">
                                        <img src={ObjProducto.image} alt="imgen de producto" />

                                        {
                                            ObjProducto.offer * 100 === 0 ? <></> :
                                                <p className="card__dcto">Dscto. {ObjProducto.offer * 100}%</p>
                                        }

                                        <p className="card__conteo">
                                            <span>
                                                {
                                                    !globalPedidos ? 0 :
                                                        actualizaQProducto(ObjProducto.pk) //cantCarritoProducto //obtieneQProductoDelPedido(ObjProducto.pk)
                                                }
                                            </span>
                                            <img src="https://res.cloudinary.com/dqcdixbty/image/upload/v1608516952/tcovid/carrito_rrwbua.svg" height="10" alt="imgen de carrito compras" />
                                        </p>
                                        <p className="card__suma" onClick={() => {
                                            modificarProductos("incrementar", ObjProducto)
                                        }}>+1</p>
                                        <p className="card__resta" onClick={() => {
                                            modificarProductos("decrementar", ObjProducto)
                                        }}>-1</p>
                                        
                                        <p className="card__empresa">{ObjProducto.store_name}</p>
                                        <p className="card__producto">{ObjProducto.name}</p>
                                    </div>

                                    <div className="card__zonaInferior">

                                        <p className="zonaInferior_datos">{ObjProducto.store_hour_sem}</p>
                                        <p className="zonaInferior_datos">{ObjProducto.store_hour_fsem}</p>
                                        <div className="card__descripcion">
                                            <img src="https://res.cloudinary.com/dqcdixbty/image/upload/v1608517163/tcovid/delivery_2_n54ykh.svg" alt="" height="20" /> Delivery
                                        </div>

                                        <p className="zonaInferior_datos card__subtitulo">Ver mas...</p>
                                        <p className="card__descripcion"><i className="far fa-building"></i>{ObjProducto.store_address}</p>

                                        <p className="card__precio">S/. {ObjProducto.price}</p>
                                    </div>
                                </div>

                            )
                        })

                        : <span>Cargando Productos...</span>
                }
            </section>

        </div >
    )
}




export default TiendaProductos
